.wf-loading {
  visibility: hidden; }

.sr-only, .screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

*, *::before, *::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  color: #202020;
  font-size: 16px;
  font-size: 100%;
  overflow-x: hidden;
  font-family: Helvetica, sans-serif;
  background: #fff; }

p em {
  font-style: italic; }

a {
  color: inherit; }

img {
  max-width: 100%;
  height: auto; }

.alert-warning {
  top: 0;
  left: 0;
  right: 0;
  z-index: 200;
  color: #202020;
  padding: 50px;
  text-align: center;
  background: #202020; }

.alert {
  padding: 50px;
  background: #fff; }

.no-scroll {
  overflow: hidden; }

.hide {
  display: none; }

.wrap {
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
  position: relative;
  *zoom: 1; }
  .wrap:before, .wrap:after {
    content: "\0020";
    display: block;
    height: 0;
    overflow: hidden; }
  .wrap:after {
    clear: both; }

.content {
  z-index: 1;
  width: 100%;
  position: relative; }

.user-content hr {
  height: 0;
  border: 0;
  border-bottom: 1px solid #fff; }
  .user-content hr + p {
    margin-bottom: 0; }

.user-content em {
  font-style: italic; }

.user-content strong {
  font-weight: bold; }

.select-wrapper {
  margin: 0;
  padding: 0;
  position: relative; }
  .select-wrapper:after {
    right: 10px;
    height: 8px;
    width: 13px;
    content: '';
    bottom: 14px;
    position: absolute;
    pointer-events: none;
    background-size: 13px 8px; }

.select-wrapper select {
  border: none;
  box-shadow: none;
  border-radius: 0;
  padding-right: 18px;
  background-color: transparent;
  background-image: none;
  -webkit-appearance: none; }

.select-wrapper select:focus {
  outline: none; }

.alignnone {
  margin-left: 0;
  margin-right: 0;
  max-width: 100%;
  height: auto; }

.aligncenter {
  display: block;
  height: auto; }

.alignleft,
.alignright {
  height: auto; }

@media (min-width: 480px) {
  .alignleft {
    float: left; }
  .alignright {
    float: right; } }
